.profile {
  width: 251px;
  margin: 0 auto;
  border-radius: 5px;
  box-shadow: 1px 1px 10px #a9bedb88;
  margin-bottom: 40px;
}

.description {
  padding-top: 32px;
  width: 100%;
  height: 256px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.avatar {
  border-radius: 50%;
  margin-bottom: 30px;
}

.name {
  font-size: 18px;
  font-weight: bold;
  color: #122236;
  margin-bottom: 12px;
}

.tag {
  font-size: 14px;
  color: #768696;
  margin-bottom: 12px;
}

.location {
  font-size: 14px;
  color: #768696;
}

.stats {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 100%;
  height: 74px;
  padding-left: 0;
  background-color: #f3f6f9;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.items {
  border-top: 2px solid #e4e9f0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.items:not(:last-child) {
  border-right: 2px solid #e4e9f0;
}

.label {
  font-size: 12px;
  color: #768696;
  margin-bottom: 8px;
}

.quantity {
  font-size: 14px;
  font-weight: bold;
  color: #1f3349;
}
