.transactionHistory {
  width: 870px;
  margin: 0 auto;
  text-align: center;
  box-shadow: 1px 1px 5px #999;
  border-radius: 5px;
}
.transactionHistory > thead > tr > th {
  color: #fff;
  background-color: #00bcd4;
  font-size: 13px;
  text-transform: uppercase;
  height: 50px;
  width: calc(100% / 3);
}
